<template>
    <div class="tw-w-100">
        <div class="tw-relative tw-rounded-md">
            <C1Label v-if="label">
                {{ label }}
            </C1Label>

            <textarea
                :id="id"
                :class="classList"
                :name="name"
                :value="value"
                :placeholder="placeholder"
                :rows="rows"
                v-on="inputListeners"
            />
        </div>

        <C1HelpText
            v-if="invalidFeedback"
            color="red"
        >
            {{ invalidFeedback }}
        </C1HelpText>
    </div>
</template>

<script>
    import C1HelpText from '@/components/C1HelpText';
    import C1Label from '@/components/C1Label';

    const INPUT_SIZES = ['sm', 'md', 'lg'];

    export default {
        name: 'C1FormTextarea',

        components: {
            C1HelpText,
            C1Label,
        },

        props: {
            value: {
                type: String,
                default: '',
            },

            id: {
                type: String,
                default: '',
            },

            placeholder: {
                type: String,
                default: '',
            },

            name: {
                type: String,
                default: '',
            },

            size: {
                type: String,
                default: 'md',
                validator: value => INPUT_SIZES.includes(value),
            },

            invalid: {
                type: Boolean,
                default: false,
            },

            invalidFeedback: {
                type: String,
                default: '',
            },

            label: {
                type: String,
                default: '',
            },

            rows: {
                type: [String, Number],
                default: 4,
            },
        },

        data() {
            return {
            };
        },

        computed: {
            classList() {
                return [
                    'tw-appearance-none tw-block tw-rounded tw-w-full tw-transition tw-duration-150 tw-ease-in-out',
                    this.getTextSizeClass,
                    this.getTextColorClass,
                    this.getBorderClass,
                    this.getPaddingClass,
                ];
            },

            getTextSizeClass() {
                const sizes = {
                    sm: 'tw-text-sm tw-leading-4',
                    md: 'tw-text-sm tw-leading-5',
                    lg: 'tw-text-base tw-leading-6',
                };

                return  sizes[this.size] || '';
            },

            getTextColorClass() {
                return 'tw-text-c1-gray-500 tw-placeholder-c1-cool-gray-300';
            },

            getBorderClass() {
                let output = 'tw-border focus:tw-outline-none ';
                if (this.invalid) {
                    output += 'tw-border-c1-red-400 focus:tw-border-c1-red-500';
                } else {
                    output += 'tw-border-c1-cool-gray-200 focus:tw-border-c1-blue-400';
                }

                return output;
            },

            getPaddingClass() {
                const sizes = {
                    sm: 'tw-py-2 tw-px-3',
                    md: 'tw-py-2 tw-px-3',
                    lg: 'tw-py-3 tw-px-4',
                };

                return  sizes[this.size] || '';
            },

            inputListeners() {
                const vm = this;
                return {
                    ...this.$listeners,
                    input(event) {
                        vm.$emit('input', event.target.value);
                    },
                    change(event) {
                        vm.$emit('change', event.target.value);
                    },
                    blur(event) {
                        vm.$emit('blur', event);
                    },
                };
            },
        },
    };
</script>
