<template>
    <div :class="classList">
        <slot />

        <button
            v-if="removable"
            type="button"
            :class="timesClassList"
            @click="onClick()"
        >
            <svg
                class="tw-h-2 tw-w-2"
                stroke="currentColor"
                fill="none"
                viewBox="0 0 8 8"
            >
                <path
                    stroke-linecap="round"
                    stroke-width="1.5"
                    d="M1 1l6 6m0-6L1 7"
                />
            </svg>
        </button>
    </div>
</template>

<script>
    import { C1_COLORS } from '@/utils/variants';

    const BADGE_SIZES = ['sm', 'md'];

    export default {
        name: 'C1Badge',

        props: {
            size: {
                type: String,
                default: 'md',
                validator: value => BADGE_SIZES.includes(value),
            },

            color: {
                type: String,
                default: 'cool-gray',
                validator: value => C1_COLORS.includes(value),
            },

            removable: {
                type: Boolean,
                default: false,
            },

            outline: {
                type: Boolean,
                default: false,
            },
        },

        computed: {
            classList() {
                return [
                    'tw-inline-flex tw-items-center tw-rounded-full tw-font-medium tw-leading-5',
                    this.getTextSizeClass,
                    this.getTextColorClass,
                    this.getBgColorClass,
                    this.getBorderColorClass,
                    this.getPaddingClass,
                    this.getHeightClass,
                ];
            },

            getTextSizeClass() {
                const sizes = {
                    sm: 'tw-text-xs',
                    md: 'tw-text-sm',
                };
                return sizes[this.size];
            },

            getTextColorClass() {
                return !this.outline ? `tw-text-white` : `tw-text-c1-${this.color}-400`;
            },

            getBgColorClass() {
                if (this.outline) {
                    return '';
                }

                return `tw-bg-c1-${this.color}-400`;
            },

            getBorderColorClass() {
                return `tw-border tw-border-c1-${this.color}-400`;
            },

            getPaddingClass() {
                const sizes = {
                    sm: 'tw-px-2',
                    md: 'tw-px-3',
                };
                return sizes[this.size];
            },

            getHeightClass() {
                const sizes = {
                    sm: 'tw-h-5',
                    md: 'tw-h-6',
                };
                return sizes[this.size];
            },

            timesClassList() {
                return [
                    'tw-flex-shrink-0 tw-inline-flex focus:tw-outline-none tw-rounded-full tw-h-4 tw-w-4 tw-flex tw-items-center tw-justify-center',
                    this.getTimesTextColor,
                    this.getTimesBgColor,
                    this.getTimesMargin,
                ];
            },

            getTimesTextColor() {
                return `tw-text-white`;
            },

            getTimesMargin() {
                const sizes = {
                    sm: 'tw--mr-1.6 tw-ml-1',
                    md: 'tw--mr-2 tw-ml-2',
                };

                return sizes[this.size];
            },

            getTimesBgColor() {
                return `tw-bg-c1-${this.color}-500`;
            },
        },

        methods: {
            onClick() {
                this.$emit('remove');
            },
        },
    };
</script>
