<template>
    <div :class="classList">
        <slot />
    </div>
</template>

<script>
    import { C1_COLORS } from '@/utils/variants';

    export default {
        name: 'C1Alert',

        props: {
            color: {
                type: String,
                default: 'cool-gray',
                validator: value => C1_COLORS.includes(value),
            },
        },

        computed: {
            classList() {
                return [
                    'tw-text-sm tw-rounded mb-4 tw-px-4 tw-py-2',
                    this.getTextColorClass,
                    this.getBgColorClass,
                    this.getBorderColorClass,
                ];
            },

            getTextColorClass() {
                return `tw-text-c1-${this.color}-400`;
            },

            getBgColorClass() {
                return `tw-bg-c1-${this.color}-50`;
            },

            getBorderColorClass() {
                return `tw-border tw-border-c1-${this.color}-400`;
            },
        },
    };
</script>
