<template>
    <div class="tw-w-full">
        <C1Label v-if="label">
            {{ label }}
        </C1Label>

        <div class="tw-relative tw-rounded-md">
            <div
                v-if="$slots['icon']"
                class="tw-absolute tw-flex tw-items-center tw-inset-y-0 tw-left-0 tw-pl-3 tw-text-c1-cool-gray-300"
            >
                <div style="width: 14px;">
                    <slot name="icon" />
                </div>
            </div>

            <input
                ref="input"
                :id="id"
                :class="inputClassList"
                :type="inputType"
                :name="name"
                :value="value"
                :placeholder="placeholder"
                v-on="inputListeners"
            >

            <div
                v-if="type === 'password'"
                class="tw-absolute tw-inset-y-0 tw-right-0 tw-pr-3 tw-flex tw-items-center tw-text-c1-cool-gray-400 tw-cursor-pointer"
                @click="toggleTextPassword()"
            >
                <div style="width: 15px;">
                    <span v-if="isPassword">
                        <svg
                            aria-hidden="true"
                            focusable="false"
                            data-icon="eye"
                            role="img"
                            xmlns="http://www.w3.org/2000/svg"
                            viewBox="0 0 576 512"
                        ><path
                            fill="currentColor"
                            d="M288 144a110.94 110.94 0 0 0-31.24 5 55.4 55.4 0 0 1 7.24 27 56 56 0 0 1-56 56 55.4 55.4 0 0 1-27-7.24A111.71 111.71 0 1 0 288 144zm284.52 97.4C518.29 135.59 410.93 64 288 64S57.68 135.64 3.48 241.41a32.35 32.35 0 0 0 0 29.19C57.71 376.41 165.07 448 288 448s230.32-71.64 284.52-177.41a32.35 32.35 0 0 0 0-29.19zM288 400c-98.65 0-189.09-55-237.93-144C98.91 167 189.34 112 288 112s189.09 55 237.93 144C477.1 345 386.66 400 288 400z"
                        /></svg>
                    </span>

                    <span v-else>
                        <svg
                            aria-hidden="true"
                            focusable="false"
                            data-icon="eye-slash"
                            role="img"
                            xmlns="http://www.w3.org/2000/svg"
                            viewBox="0 0 640 512"
                            class="fa-w-20"
                        ><path
                            fill="currentColor"
                            d="M634 471L36 3.51A16 16 0 0 0 13.51 6l-10 12.49A16 16 0 0 0 6 41l598 467.49a16 16 0 0 0 22.49-2.49l10-12.49A16 16 0 0 0 634 471zM296.79 146.47l134.79 105.38C429.36 191.91 380.48 144 320 144a112.26 112.26 0 0 0-23.21 2.47zm46.42 219.07L208.42 260.16C210.65 320.09 259.53 368 320 368a113 113 0 0 0 23.21-2.46zM320 112c98.65 0 189.09 55 237.93 144a285.53 285.53 0 0 1-44 60.2l37.74 29.5a333.7 333.7 0 0 0 52.9-75.11 32.35 32.35 0 0 0 0-29.19C550.29 135.59 442.93 64 320 64c-36.7 0-71.71 7-104.63 18.81l46.41 36.29c18.94-4.3 38.34-7.1 58.22-7.1zm0 288c-98.65 0-189.08-55-237.93-144a285.47 285.47 0 0 1 44.05-60.19l-37.74-29.5a333.6 333.6 0 0 0-52.89 75.1 32.35 32.35 0 0 0 0 29.19C89.72 376.41 197.08 448 320 448c36.7 0 71.71-7.05 104.63-18.81l-46.41-36.28C359.28 397.2 339.89 400 320 400z"
                            class=""
                        /></svg>
                    </span>
                </div>
            </div>

            <button
                v-if="showX && value"
                type="button"
                @click.stop.prevent="$emit('input', '')"
                class="tw-absolute tw-right-0 tw-top-0 tw-inset-y-0 tw-right-0 tw-pr-3 tw-flex tw-items-center tw-cursor-pointer tw-text-c1-cool-gray-300 focus:tw-outline-none hover:tw-text-c1-cool-gray-400"
            >
                <svg
                    class="tw-h-2 tw-w-2"
                    stroke="currentColor"
                    fill="none"
                    viewBox="0 0 8 8"
                >
                    <path
                        stroke-linecap="round"
                        stroke-width="1.5"
                        d="M1 1l6 6m0-6L1 7"
                    />
                </svg>
            </button>
        </div>

        <C1HelpText
            v-if="invalidFeedback"
            color="red"
        >
            {{ invalidFeedback }}
        </C1HelpText>
    </div>
</template>

<script>
    import C1HelpText from '@/components/C1HelpText';
    import C1Label from '@/components/C1Label';

    const INPUT_SIZES = ['sm', 'md', 'lg'];
    const INPUT_TYPES = ['text', 'password', 'email', 'number'];

    export default {
        name: 'C1FormInput',

        components: {
            C1HelpText,
            C1Label,
        },

        props: {
            value: {
                type: String,
                default: undefined,
            },

            id: {
                type: String,
                default: undefined,
            },

            type: {
                type: String,
                default: 'text',
                validator: value => INPUT_TYPES.includes(value),
            },

            placeholder: {
                type: String,
                default: '',
            },

            name: {
                type: String,
                default: '',
            },

            size: {
                type: String,
                default: 'md',
                validator: value => INPUT_SIZES.includes(value),
            },

            invalid: {
                type: Boolean,
                default: false,
            },

            invalidFeedback: {
                type: String,
                default: '',
            },

            label: {
                type: String,
                default: '',
            },

            showX: {
                type: Boolean,
                default: false,
            },

            naked: {
                type: Boolean,
                default: false,
            },
        },

        data() {
            return {
                inputType: this.type,
            };
        },

        computed: {
            inputClassList() {
                return [
                    'tw-appearance-none tw-rounded tw-w-full tw-transition tw-duration-150 tw-ease-in-out',
                    this.getTextSizeClass,
                    this.getTextColorClass,
                    this.getBorderColorClass,
                    this.getPaddingClass,
                ];
            },

            getTextSizeClass() {
                const sizes = {
                    sm: 'tw-text-sm tw-leading-4',
                    md: 'tw-text-sm tw-leading-5',
                    lg: 'tw-text-base tw-leading-6',
                };

                return  sizes[this.size] || '';
            },

            getTextColorClass() {
                return 'tw-text-c1-gray-500 tw-placeholder-c1-cool-gray-300';
            },

            getBorderColorClass() {
                let output = 'focus:tw-outline-none';
                if (this.naked) {
                    return output;
                }

                output += ' tw-border';

                if (this.invalid) {
                    output += ' tw-border-c1-red-400 focus:tw-border-c1-red-500';
                } else {
                    output += ' tw-border-c1-cool-gray-200 focus:tw-border-c1-blue-400';
                }

                return output;
            },

            getPaddingClass() {
                const sizes = {
                    sm: `tw-py-2 ${this.isPassword ? 'tw-pr-10' : 'tw-pr-3'} ${this.$slots['icon'] ? 'tw-pl-8' : 'tw-pl-3'}`,
                    md: `tw-h-10 tw-py-2 ${this.isPassword ? 'tw-pr-10' : 'tw-pr-3'} ${this.$slots['icon'] ? 'tw-pl-8' : 'tw-pl-3'}`,
                    lg: `tw-py-3 ${this.isPassword ? 'tw-pr-10' : 'tw-pr-5'} ${this.$slots['icon'] ? 'tw-pl-8' : 'tw-pl-5'}`,
                };

                return  sizes[this.size] || '';
            },

            isPassword() {
                return this.inputType === 'password';
            },

            inputListeners() {
                const vm = this;
                return {
                    ...this.$listeners,
                    input(event) {
                        if (typeof vm.value !== 'undefined') {
                            vm.$emit('input', event.target.value);
                        }
                    },
                    change(event) {
                        if (typeof vm.value !== 'undefined') {
                            vm.$emit('input', event.target.value);
                        }
                    },
                    blur(event) {
                        vm.$emit('blur', event);
                    },
                };
            },
        },

        methods: {
            toggleTextPassword() {
                this.inputType = this.isPassword ? 'text' : 'password';
            },
        },
    };
</script>
