<template>
    <small :class="classList">
        <slot />
    </small>
</template>

<script>
    import { C1_COLORS } from '@/utils/variants';

    export default {
        name: 'C1HelpText',

        props: {
            color: {
                type: String,
                default: 'cool-gray',
                validator: value => C1_COLORS.includes(value),
            },
        },

        computed: {
            classList() {
                return [
                    'tw-text-xs tw-m-0 tw-py-1 tw-leading-3',
                    this.getTextColorClass,
                ];
            },

            getTextColorClass() {
                return `tw-text-c1-${this.color}-400`;
            },
        },
    };
</script>
