<template>
    <Component
        :is="disabled ? 'span' : 'button'"
        :type="type"
        :class="classList"
        v-on="listeners"
    >
        <slot />
    </Component>
</template>

<script>
    export default {
        name: 'C1PaginatorPage',

        props: {
            disabled: {
                type: Boolean,
                default: false,
            },

            active: {
                type: Boolean,
                default: false,
            },
        },

        data() {
            return {
            };
        },

        computed: {
            classList() {
                return [
                    'tw--ml-px tw-relative tw-inline-flex tw-items-center tw-px-4 tw-py-2 tw-border tw-text-sm focus:tw-outline-none tw-leading-5 tw-font-medium tw-transition tw-ease-in-out tw-duration-150',
                    this.getTextColorClass,
                    this.getBorderColorClass,
                    this.getBgColorClass,
                    this.getZindexClass,
                ];
            },

            getTextColorClass() {
                if (this.active) {
                    return `tw-text-white`;
                }

                return `tw-text-c1-cool-gray-400 ${!this.disabled ? 'hover:tw-text-c1-blue-500 active:tw-text-white' : ''}`;
            },

            getBorderColorClass() {
                if (this.active) {
                    return '';
                }

                return this.disabled ? 'tw-border-c1-cool-gray-100' : 'tw-border-c1-cool-gray-100 hover:tw-border-c1-blue-400';
            },

            getBgColorClass() {
                if (this.active) {
                    return `tw-bg-c1-blue-400`;
                }

                return this.disabled ? 'tw-bg-white' : 'tw-bg-white active:tw-bg-c1-blue-400';
            },

            getZindexClass() {
                return 'focus:tw-z-10 hover:tw-z-10';
            },

            type() {
                return this.disabled ? undefined : 'button';
            },

            listeners() {
                return this.$listeners;
            },
        },

        methods: {},
    };
</script>
