                            focusable="false"
                            data-prefix="fal"
                            data-icon="search"
                            role="img"
                            xmlns="http://www.w3.org/2000/svg"
                            viewBox="0 0 512 512"
                        ><path
                            data-v-8dab3068=""
                            fill="currentColor"
                            d="M508.5 481.6l-129-129c-2.3-2.3-5.3-3.5-8.5-3.5h-10.3C395 312 416 262.5 416 208 416 93.1 322.9 0 208 0S0 93.1 0 208s93.1 208 208 208c54.5 0 104-21 141.1-55.2V371c0 3.2 1.3 6.2 3.5 8.5l129 129c4.7 4.7 12.3 4.7 17 0l9.9-9.9c4.7-4.7 4.7-12.3 0-17zM208 384c-97.3 0-176-78.7-176-176S110.7 32 208 32s176 78.7 176 176-78.7 176-176 176z"
                            class=""
                        /></svg>
                    </slot>
                </div>
            </div>

            <input
                ref="input"
                type="text"
                :placeholder="placeholder"
                :value="keyword"
                :class="inputClassList"
                @input="onInput($event.target.value)"
                @focus="isFocused = true"
                @blur="onBlur"
                @keydown="onKeydown"
                v-on="inputListeners"
            >

            <button
                v-if="keyword"
                type="button"
                @click.stop.prevent="onClear()"
                class="tw-absolute tw-right-0 tw-top-0 tw-inset-y-0 tw-right-0 tw-pr-3 tw-flex tw-items-center tw-cursor-pointer tw-text-c1-cool-gray-300 focus:tw-outline-none hover:tw-text-c1-cool-gray-400"
            >
                <svg
                    class="tw-h-2 tw-w-2"
                    stroke="currentColor"
                    fill="none"
                    viewBox="0 0 8 8"
                >
                    <path
                        stroke-linecap="round"
                        stroke-width="1.5"
                        d="M1 1l6 6m0-6L1 7"
                    />
                </svg>
            </button>
        </div>

        <C1HelpText
            v-if="invalidFeedback"
            color="red"
        >
            {{ invalidFeedback }}
        </C1HelpText>
