        <!--                    class=""-->
        <!--                /></svg>-->
        <!--            </slot>-->
        <!--        </C1FormInput>-->

        <div
            v-if="isFocused && !mutableOptions.length && keyword.length >= searchMinLength && $slots['noResults']"
            class="tw-absolute tw-right-0 tw-mt-2 tw-w-full tw-rounded-md tw-shadow-lg tw-z-50"
        >
            <div class="tw-text-sm tw-p-3 tw-rounded-md tw-bg-white tw-shadow-xs tw-text-c1-cool-gray-300 tw-text-medium">
                <slot name="noResults" />
            </div>
        </div>

        <div
            v-show="isFocused && mutableOptions.length && keyword.length >= searchMinLength"
            class="tw-absolute tw-right-0 tw-mt-2 tw-w-full tw-rounded-md tw-shadow-lg tw-z-50 tw-overflow-y-scroll"
            style="max-height: 200px;"
        >
            <ul class="tw-py-1 tw-rounded-md tw-bg-white tw-shadow-xs">
                <li
                    v-for="(opt, index) in mutableOptions"
                    :key="`option_${opt[valueKey]}_${index}`"
                    :class="{ 'tw-bg-c1-cool-gray-50': arrowCounter === index }"
                    :ref="`option_${index}`"
                    class="c1-typeahead-item tw-block tw-px-4 tw-py-2 tw-text-sm tw-leading-5 tw-text-c1-cool-gray-700 tw-cursor-pointer"
                    tabindex="0"
                    @click="onSelect()"
                    @mouseover="setArrowCounter(index)"
                >
                    <span
                        v-html="opt[`${labelKey}_highlighted`] || opt[labelKey]"
                        class="tw-font-semibold"
                    />
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import C1HelpText from '@/components/C1HelpText';

    const INPUT_SIZES = ['sm', 'md', 'lg'];

    export default {
        name: 'C1Typeahead',

        components: {
            C1HelpText,
        },

        props: {
            value: {
                type: String,
                default: '',
            },

            placeholder: {
                type: String,
                default: '',
            },

            options: {
                type: Array,
                default: () => [],
            },

            labelKey: {
                type: String,
                default: 'label',
            },

            valueKey: {
                type: String,
                default: 'id',
            },

            filterBy: {
                type: String,
                default: 'label',
            },

            searchMinLength: {
                type: Number,
                default: 3,
            },

            cleanAfterSelect: {
                type: Boolean,
                default: false,
            },

            size: {
                type: String,
                default: 'md',
                validator: value => INPUT_SIZES.includes(value),
            },

            naked: {
                type: Boolean,
                default: false,
            },

            invalid: {
                type: Boolean,
                default: false,
            },

            invalidFeedback: {
                type: String,
                default: '',
            },
        },

        data() {
            return {
                isFocused: false,
                keyword: '',
                id: this._uid,
                arrowCounter: 0,
                originalOptions: [],
                mutableOptions: [],
            };
        },

        computed: {
            inputClassList() {
                return [
                    'tw-appearance-none tw-rounded tw-w-full tw-transition tw-duration-150 tw-ease-in-out',
                    this.getTextSizeClass,
                    this.getTextColorClass,
                    this.getBorderColorClass,
                    this.getPaddingClass,
                ];
            },

            getTextSizeClass() {
                const sizes = {
                    sm: 'tw-text-sm tw-leading-4',
                    md: 'tw-text-sm tw-leading-5',
                    lg: 'tw-text-base tw-leading-6',
                };

                return  sizes[this.size] || '';
            },

            getTextColorClass() {
                return 'tw-text-c1-gray-500 tw-placeholder-c1-cool-gray-300';
            },

            getBorderColorClass() {
                let output = 'focus:tw-outline-none';
                if (this.naked) {
                    return output;
                }

                output += ' tw-border';

                if (this.invalid) {
                    output += ' tw-border-c1-red-400 focus:tw-border-c1-red-500';
                } else {
                    output += ' tw-border-c1-cool-gray-200 focus:tw-border-c1-blue-400';
                }

                return output;
            },

            getPaddingClass() {
                const sizes = {
                    sm: 'tw-py-2 tw-pr-6 tw-pl-8',
                    md: 'tw-h-10 tw-py-5 tw-pr-6 tw-pl-8',
                    lg: 'tw-py-3 tw-pr-6 tw-pl-8',
                };

                return  sizes[this.size] || '';
            },

            inputListeners() {
                const vm = this;
                return {
                    ...this.$listeners,
                    input(event) {
                        if (typeof vm.value !== 'undefined') {
                            vm.$emit('input', event.target.value);
                        }
                    },
                    change(event) {
                        if (typeof vm.value !== 'undefined') {
                            vm.$emit('input', event.target.value);
                        }
                    },
                    blur(event) {
                        vm.$emit('blur', event);
                    },
                };
            },
        },

        watch: {
            value(value) {
                this.keyword = value;
            },

            options() {
                this.cloneOptions();
            },
        },

        created() {
            this.keyword = this.value;

            if (this.options.length) {
                this.cloneOptions();
            }
        },

        methods: {
            onInput(newVl) {
                const oldVl = this.keyword;
                const first3New = newVl.toLowerCase().substring(0, 3);
                const first3Old = oldVl.toLowerCase().substring(0, 3);
                const isFirst3Matched = first3New === first3Old;

                this.keyword = newVl;
                this.emitInput();

                if (newVl.length >= this.searchMinLength) {
                    if (!isFirst3Matched || !this.originalOptions.length) {
                        this.$emit('shouldSearch', first3New);
                    } else {
                        this.searchInternally();
                    }
                } else {
                    this.resetOptions();
                }
            },

            onSelect() {
                const selected = this.mutableOptions[this.arrowCounter];
                const selectedOption = this.options.find(o => o[this.valueKey] === selected[this.valueKey]);
                if (selectedOption) {
                    this.$emit('select', selectedOption);
                    this.keyword = selectedOption[this.labelKey];
                    this.emitInput();
                    this.resetArrowCounter();

                    if (this.cleanAfterSelect) {
                        this.resetKeyword();
                    }

                    this.resetOptions();
                }
            },

            searchInternally() {
                const search = this.keyword.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
                this.mutableOptions = this.originalOptions.filter((o) => o[this.filterBy].toLowerCase().search(search.toLowerCase()) >= 0);
                this.highlightOptions();
            },

            highlightOptions() {
                const search = this.keyword.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
                const query = new RegExp(search, 'i');
                this.mutableOptions.forEach((o) => {
                    this.$set(o, `${this.labelKey}_highlighted`, o[this.labelKey].replace(query, '<span class="tw-font-normal">$&</span>'));
                });
            },

            cloneOptions() {
                this.originalOptions = JSON.parse(JSON.stringify(this.options));
                this.mutableOptions = JSON.parse(JSON.stringify(this.options));
                this.searchInternally();
            },

            onBlur(evt) {
                const tgt = evt.relatedTarget;
                if (tgt && tgt.classList.contains('c1-typeahead-item')) { return; }

                this.$emit('blur', evt);

                this.isFocused = false;
            },

            onKeydown(evt) {
                if (!this.mutableOptions.length) { return; }

                switch (evt.code) {
                    case 'ArrowUp':
                        evt.preventDefault();
                        this.onArrowUp();
                        break;
                    case 'ArrowDown':
                        evt.preventDefault();
                        this.onArrowDown();
                        break;
                    case 'Enter':
                        this.onSelect();
                        break;
                    case 'Escape':
                        this.onEsc();
                        break;
                }
            },

            onEsc() {
                this.$refs.input.blur();
                this.isFocused = false;
                this.resetArrowCounter();
            },

            onArrowDown() {
                if (this.arrowCounter < this.mutableOptions.length - 1) {
                    this.arrowCounter += 1;
                }
                this.fixScrolling();
            },

            onArrowUp() {
                if (this.arrowCounter > 0) {
                    this.arrowCounter -= 1;
                }
                this.fixScrolling();
            },

            fixScrolling() {
                this.$refs[`option_${this.arrowCounter}`][0].scrollIntoView({ behavior: 'smooth', block: 'nearest', inline: 'start' });
            },

            setArrowCounter(number) {
                this.arrowCounter = number;
            },

            resetArrowCounter() {
                this.arrowCounter = 0;
            },

            emitInput() {
                this.$emit('input', this.keyword);
            },

            resetOptions() {
                this.originalOptions = [];
                this.mutableOptions = [];
            },

            resetKeyword() {
                this.keyword = '';
                this.emitInput();
            },

            onClear() {
                this.$emit('select', null);
                this.resetKeyword();
                this.resetOptions();
            },
        },
    };
</script>
