<template>
    <div class="tw-relative tw-inline-block tw-text-left">
        <button
            type="button"
            :class="buttonClassList"
            @click.stop.prevent="isOpen = !isOpen"
        >
            <slot name="button-content">
                {{ text }}
            </slot>

            <slot
                v-if="!noIcon"
                name="button-icon"
            >
                <svg
                    :class="iconClassList"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                >
                    <path
                        fill-rule="evenodd"
                        d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z"
                        clip-rule="evenodd"
                    />
                </svg>
            </slot>
        </button>

        <Transition
            enter-active-class="tw-transition tw-ease-out tw-duration-100"
            enter-class="tw-transform tw-opacity-0 tw-scale-95"
            enter-to-class="tw-transform tw-opacity-100 tw-scale-100"
            leave-active-class="tw-transition tw-ease-in tw-duration-75"
            leave-class="tw-transform tw-opacity-100 tw-scale-100"
            leave-to-class="tw-transform tw-opacity-0 tw-scale-95"
        >
            <div
                v-show="isOpen"
                class="tw-origin-top-right tw-absolute tw-right-0 tw-mt-2 tw-w-56 tw-rounded-md tw-shadow-lg tw-z-50"
            >
                <div class="tw-py-1 tw-rounded-md tw-bg-white tw-shadow-xs">
                    <slot>
                        <ul>
                            <li
                                v-for="(opt, index) in options"
                                :key="`option_${opt[labelKey]}_${index}`"
                                class="tw-block tw-px-4 tw-py-2 tw-text-sm tw-leading-5 tw-text-gray-700 hover:tw-bg-gray-100 hover:tw-text-gray-900 focus:tw-outline-none focus:tw-bg-gray-100 focus:tw-text-gray-900 tw-cursor-pointer"
                                @click="select(opt)"
                            >
                                {{ opt[label] }}
                            </li>
                        </ul>
                    </slot>
                </div>
            </div>
        </Transition>
    </div>
</template>

<script>
    export default {
        name: 'C1Dropdown',

        props: {
            naked: {
                type: Boolean,
                default: false,
            },

            noPadding: {
                type: Boolean,
                default: false,
            },

            noIcon: {
                type: Boolean,
                default: false,
            },

            btnClass: {
                type: String,
                default: '',
            },

            iconClass: {
                type: String,
                default: '',
            },

            size: {
                type: String,
                default: 'md',
                validator: value => ['sm', 'md'].includes(value),
            },

            rounded: {
                type: [String, Boolean],
                default: undefined,
                validator: value => ['', 'sm', 'md', 'lg', 'full'].includes(value),
            },

            text: {
                type: String,
                default: '',
            },

            options: {
                type: Array,
                default: () => [],
            },

            label: {
                type: String,
                default: 'label',
            },

            labelKey: {
                type: String,
                default: 'id',
            },
        },

        data() {
            return {
                isOpen: false,
                id: this._uid,
            };
        },

        computed: {
            buttonClassList() {
                return [
                    'tw-flex tw-items-center tw-justify-center tw-leading-5 tw-transition tw-ease-in-out tw-duration-150 tw-z-50',
                    this.getTextSizeClass,
                    this.getTextColorClass,
                    this.getPaddingClass,
                    this.getBgClass,
                    this.getBtnExtraClasses,
                    this.getBorderRoundedClass,
                    this.getBorderClass,
                    this.getShadowClass,
                ];
            },

            getTextSizeClass() {
                const sizes = {
                    sm: 'tw-text-xs tw-font-medium',
                    md: 'tw-text-sm tw-font-medium',
                };

                return sizes[this.size];
            },

            getTextColorClass() {
                return !this.naked ? 'tw-text-cool-gray-700 hover:tw-text-cool-gray-500 active:tw-text-cool-gray-700' : 'tw-text-cool-gray-400';
            },

            getShadowClass() {
                return !this.naked ? 'tw-shadow-sm' : '';
            },

            getBorderClass() {
                return !this.naked ? 'tw-border tw-border-cool-gray-300 focus:tw-border-blue-300 focus:tw-outline-none focus:tw-shadow-outline-blue' : 'focus:tw-outline-none';
            },

            getBorderRoundedClass() {
                if (this.naked) {
                    return '';
                }

                if (this.rounded === '') {
                    return 'tw-rounded';
                }
                return this.rounded ? `tw-rounded-${this.rounded}` : '';
            },

            getPaddingClass() {
                if (this.noPadding) {
                    return '';
                }

                const sizes = {
                    sm: 'tw-px-2 tw-py-1',
                    md: 'tw-px-4 tw-py-2',
                };

                return sizes[this.size];
            },

            getBgClass() {
                return !this.naked ? 'tw-bg-white active:tw-bg-gray-50' : '';
            },

            getBtnExtraClasses() {
                return this.btnClass;
            },

            iconClassList() {
                if (this.iconClass) {
                    return this.iconClass;
                }

                return [
                    this.getIconDimensionClass,
                    this.getIconMarginClass,
                ];
            },

            getIconDimensionClass() {
                const sizes = {
                    sm: 'h-3 w-4',
                    md: 'h-4 w-4',
                };

                return sizes[this.size];
            },

            getIconMarginClass() {
                const sizes = {
                    sm: `${!this.naked ? '-mr-1' : ''} ml-1`,
                    md: `${!this.naked ? '-mr-1' : ''} ml-2`,
                };

                return sizes[this.size];
            },
        },

        watch: {
            isOpen(value) {
                if (value) {
                    this.$root.$emit('tw::dropdown::shown', this);
                }
            },
        },

        created() {
            this.$root.$on('tw::dropdown::shown', this.rootCloseListener);
        },

        mounted() {
            if (typeof document !== 'undefined') {
                document.addEventListener('click', this.clickOutListener);
            }
        },

        beforeDestroy() {
            if (typeof document !== 'undefined') {
                document.removeEventListener('click', this.clickOutListener);
            }
        },

        methods: {
            rootCloseListener(vm) {
                if (vm !== this) {
                    this.close();
                }
            },

            clickOutListener(e) {
                // TODO Refactor as it should close only the current scope
                if (!this.$el.contains(e.target)) {
                    this.close();
                }
            },

            close() {
                this.isOpen = false;
            },

            select(opt) {
                this.close();
                this.$emit('select', opt);
            },
        },
    };
</script>
