<template>
    <button
        :type="type"
        :disabled="busy"
        :class="classList"
        v-on="inputListeners"
    >
        <C1Spinner
            v-if="busy"
            class="tw-mr-2"
        />

        <slot />
    </button>
</template>

<script>
    import { C1_COLORS, C1_SHADOWS } from '../utils/variants';
    import C1Spinner from './C1Spinner';

    const BTN_SIZES = ['xs', 'sm', 'md', 'lg', 'xl'];

    export default {
        name: 'C1Button',

        components: {
            C1Spinner,
        },

        props: {
            type: {
                type: String,
                default: 'button',
            },

            color: {
                type: String,
                default: null,
                validator: value => C1_COLORS.includes(value),
            },

            btnClass: {
                type: String,
                default: '',
            },

            size: {
                type: String,
                default: 'md',
                validator: value => BTN_SIZES.includes(value),
            },

            shadow: {
                type: String,
                default: null,
                validator: value => C1_SHADOWS.includes(value),
            },

            outline: {
                type: Boolean,
                default: false,
            },

            block: {
                type: Boolean,
                default: false,
            },

            busy: {
                type: Boolean,
                default: false,
            },
        },

        data() {
            return {};
        },

        computed: {
            classList() {
                if (this.btnClass) {
                    return this.btnClass;
                }

                return [
                    'tw-text-center tw-inline-flex tw-items-center tw-justify-center tw-rounded tw-text-white tw-transition tw-ease-in-out tw-duration-150',
                    this.getTextSizeClass,
                    this.getTextWeightClass,
                    this.getTextColorClass,
                    this.getPaddingClass,
                    this.getBorderColorClass,
                    this.getBgColorClass,
                    this.getBlockClass,
                    this.getDisabledClass,
                ];
            },

            getTextSizeClass() {
                const sizes = {
                    xs: 'tw-text-xs tw-leading-4',
                    sm: 'tw-text-xs tw-leading-4',
                    md: 'tw-text-sm tw-leading-5',
                    lg: 'tw-text-base tw-leading-6',
                    xl: 'tw-text-base tw-leading-6',
                };

                return  sizes[this.size] || '';
            },

            getTextColorClass() {
                if (!this.color) {
                    return 'tw-text-c1-cool-gray-400 hover:tw-text-c1-cool-gray-500';
                }

                if (this.outline) {
                    return `tw-text-c1-${this.color}-400 hover:tw-text-c1-${this.color}-500`;
                }

                return 'tw-text-white';
            },

            getTextWeightClass() {
                return 'tw-font-medium';
            },

            getPaddingClass() {
                const paddings = {
                    xs: 'tw-px-2 tw-py-1',
                    sm: 'tw-px-3 tw-py-2',
                    md: 'tw-h-10 tw-px-4 tw-py-2',
                    lg: 'tw-px-4 tw-py-2',
                    xl: 'tw-px-6 tw-py-3',
                };

                return paddings[this.size] || '';
            },

            getBorderColorClass() {
                if (!this.color) {
                    return 'tw-border-none';
                }

                if (this.outline) {
                    return `tw-border tw-border-c1-${this.color}-400 hover:tw-border-c1-${this.color}-500`;
                }

                return `focus:tw-outline-none focus:tw-shadow-outline-c1-${this.color}-50`;
            },

            getBgColorClass() {
                if (this.outline) {
                    return '';
                }

                const bg = {
                    normal: `tw-bg-c1-${this.color}-400`,
                    hover: `${!this.busy ? `hover:tw-bg-c1-${this.color}-500` : ''}`,
                    focus: `focus:tw-border-c1-${this.color}-500`,
                    active: `active:tw-bg-c1-${this.color}-500`,
                };

                return Object.values(bg).join(' ');
            },

            getBlockClass() {
                return this.block ? 'tw-w-full tw-justify-center' : '';
            },

            getDisabledClass() {
                return this.busy ? 'tw-opacity-75' : '';
            },

            inputListeners() {
                return this.$listeners;
            },
        },
    };
</script>
